import React from 'react';


const Row = ({ left, right }) => {
    
    return (
        <div className="row">
            <div className="col-md-6 col-10">
                { left }
            </div>
            <div className="col-md-6 col-10">
                { right }
            </div>
        </div>
    );
};

export default Row;